/*=============== VARIABLES CSS ===============*/
:root {
    /*========== Colors ==========*/
    --header-column-background: #24305a;
    --border-color: #141b35;
    --canvas-background: #060a19;
    --main-color: 	#f3f6f4;
  
    /*========== Font and typography ==========*/
    --body-font: "Pixelify Sans", sans-serif;
    --biggest-font-size: 2.5rem;
    --h1-font-size: 2rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1.125rem;
    --normal-font-size: .938rem;
    --small-font-size: .813rem;
    --smaller-font-size: .75rem;
  
    /*========== Font weight ==========*/
    --font-regular: 400;
    --font-medium: 500;
    --font-semi-bold: 600;
    --font-bold: 700;
}

/*=============== BASE ===============*/
body {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    height: 100vh;
    font-family: var(--body-font);
    color: var(--main-color)
}

button {
    font-family: var(--body-font);
}

ul{
    margin-block-start: 0px;
    margin-block-end: 0px;
    padding-inline-start: 0px;
}

li {
    list-style-type: none;
}

h1 {
    font-weight: lighter;
    font-size: var(--h1-font-size);
    margin-block-start: 0px;
    margin-block-end: 0px;
}

/*=============== HEADER ===============*/
#headerContainer{
    display: flex;
    background-color: var(--header-column-background);
    border: 1px solid var(--border-color);
    flex: 0 0 auto;
    width: 100%;
    align-items: center;
}

.title{
    padding-left: 10px;
}

/* Dropdown Button */
.dropbtn {
    background-color: #04AA6D;
    color: white;
    padding: 8px;
    font-size: var(--h3-font-size);
    border: none;
  }
  
  /* The container <div> - needed to position the dropdown content */
  .settingsBar {
    position: relative;
    display: inline-block;
    padding-left: 10px;
  }
  
  /* Dropdown Content (Hidden by Default) */
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 3;
  }
  
  /* Links inside the dropdown */
  .dropdown-content div {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  
  /* Change color of dropdown links on hover */
  .dropdown-content div:hover {background-color: #ddd;}
  
  /* Show the dropdown menu on hover */
  .settingsBar:hover .dropdown-content {display: block;}
  
  /* Change the background color of the dropdown button when the dropdown content is shown */
  .settingsBar:hover .dropbtn {background-color: #3e8e41;}

/*=============== MAIN ===============*/
.mainContainer{
    display: flex;
    flex: 1; /* Allow the container to grow and fill the available space */
}

/*=============== LEFT COLUMN ===============*/
.leftCol{
    background-color: var(--header-column-background);
    border: 1px solid var(--border-color);
    flex-grow: 1; /* Allow leftCol to grow and fill the remaining space */
}

.toolBarList{
    width: 34px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.toolButton:hover{
    background-color: var(--main-color);
}

/*=============== CENTER COLUMN ===============*/
.centerCol {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; /* Center the content vertically */
    width: 100vw;
    background-color: var(--canvas-background);
}

.canvasWrapper {
    margin-top: 20px;
    margin-bottom: 20px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center; /* Center the canvases */
    overflow-y: scroll;
    overflow-x: scroll;
    width: 100%;
    height: 100%;
}

#backgroundCanvas, #pixelCanvas {
    
    position: absolute;
    display: block;
}

#backgroundCanvas {
    z-index: 1;
}

#pixelCanvas {
    z-index: 2;
}

/*=============== RIGHT COLUMN ===============*/
.rightCol{
    background-color: var(--header-column-background);
    border: 1px solid var(--border-color);
}

.colorPickerImg_Container{
    width: 200px;
    padding: 10px;
}

.colorInfoContainer {
    display: flex;
    flex-direction: column;
}

.promptBox{
    display: flex;
    gap: 10px;
}

.colorPrompt{
    margin-block-start: 2px;
    margin-block-end: 2px;
}

.colorBox {
    width: 15px;
    height: 15px;
    border: 1px solid var(--main-color);
    align-self: center;
}

/*=============== MODAL ===============*/
.modal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
    padding-top: 60px;
}

.modal-content {
    background-color: #fefefe;
    margin: 5% auto; /* 15% from the top and centered */
    padding: 20px;
    border: 1px solid #888;
    width: 80%; /* Could be more or less, depending on screen size */
}

.close-button {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close-button:hover,
.close-button:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}
